.todoBox {
  position: fixed;
  top: 10%;
  left: 50%;
  transform: translatex(-50%);
  width: 60%;
  height: 70%;
  background-color: rgba(255, 255, 255, 0.7);
  border: 5px solid snow;
  border-radius: 15px;
  text-align: center;
}
.title {
  color: rgb(53, 51, 49);
}
.txtInput {
  border: none;
  border-bottom: 2px solid rgb(53, 51, 49);
  background-color: transparent;
  width: 65%;
  margin-top: 40px;
  outline: none;
  height: 30px;
  font-size: 18px;
}
.addBtn {
  margin-left: 10px;
  width: 30px;
  height: 30px;
  border-radius: 50px;
  border: 1px solid rgb(53, 51, 49);
  padding: 0 auto;
}
.todoList {
  list-style: none;
  margin: 50px auto 0;
  padding: 0;
}
.todoItem {
  margin: 0 auto;
  margin-top: 10px;
  width: 70%;
  height: 40px;
  line-height: 40px;
  background-color: snow;
  border-radius: 5px;
  text-align: left;
  padding: 0 15px;
}
.todoItemInput {
  margin-right: 10px;
}
.removeBtn {
  position: absolute;
  right: 8vw;
  margin-top: 9px;
  border: none;
  background-color: transparent;
}
